  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  display: inline-block;
  justify-content: center;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #4CAF50;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  display: inline-block;
  justify-content: center;
  border: none;
}
.boton-verde:hover {
  background-color: #449d48;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #4CAF50;
  color: #FFFFFF;
  font-weight: 700;
